const StatisticRaw = () => {
  const actions = useMappedActions()

  const onClickDelete = ({ presetName }) => {
    console.log('onClickDelete Statistic', presetName)
  }

  const onChangeMetric = ({ value }) => {
    console.log('onChangeMetric Statistic', value)
  }

  const onChangePreset = ({ value }) => {
    console.log('onChangePreset Statistic', value)
  }

  const onClickEdit = useCallback(
    ({ value }) => {
      actions.showModal(modalConstants.EDIT_PRESET_MODAL, { presetName: 123132, onClickDelete })

      console.log('onChangePreset Statistic', value)
    },
    [actions],
  )

  const reportLink = useMemo(() => {
    return (
      <div className={styles.label}>
        <a className={styles.link} href="#">
          <span className={styles.linkText}>Download report</span>
          <LinkIcon className={styles.linkIcon} />
        </a>
      </div>
    )
  }, [])

  const rightPartMetrics = useMemo(() => {
    return (
      <div className={styles.metrics}>
        <Dropdown
          name="metric"
          onChange={(value) => onChangeMetric(value)}
          key="metric"
          placeholder="Selected metric"
          options={metrics}
          hasError={false}
          isSimple
        />
      </div>
    )
  }, [])

  const rightPartReport = useMemo(() => {
    return (
      <div className={styles.preset}>
        <div className={styles.presetSelect}>
          <Dropdown
            name="Preset"
            onChange={(value) => onChangePreset(value)}
            key="Preset"
            placeholder="Name preset"
            options={presets}
            hasError={false}
            isSimple
          />
        </div>
        <div className={styles.presetAction}>
          <BaseButton
            className={styles.presetBtn}
            text={
              <span>
                <EditIcon /> Edit
              </span>
            }
            skin="yellow"
            disabled={false}
            onClick={(value) => onClickEdit(value)}
          />
        </div>
      </div>
    )
  }, [onClickEdit])

  return (
    <Fragment>
      <StatisticFilter />
      <Accordeon title="Compressed metrics — table" initialState={true} content={<MetricList />} />
      <Accordeon
        title="Compressed metrics — graphic"
        skin="white"
        content={<LinearChart />}
        rightPart={rightPartMetrics}
      />
      <Accordeon title="Geographical distribution" skin="white" content={<MapChart />} />
      <Accordeon
        title="Aggregated performance report"
        skin="white"
        content={<Report />}
        label={reportLink}
        rightPart={rightPartReport}
      />
    </Fragment>
  )
}

export const Statistic = StatisticRaw
export default memo(Statistic)
